body {
    font-family: "Helvetica", sans-serif;
    /*min-height: 100vh;*/
    background-color: #f8f9fa;
  }

.login-page-container {
  height: 100vh;
}

  .school-logo {
    width: 100px;
    height: 100px;
    position: absolute;
    top: 15px;
    left: 15px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  .login-container {
    width: 400px;
    background-color: #ffffff;
    padding: 40px;
    border: 1px solid #d9d9d9;
    border-radius: 8px;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.1);
  }
  .login-container h1 {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 10px;
    color: #000000;
  }
  .checkbox-container {
    margin-top: -10px;
  }

  .login-container p {
    margin-bottom: 20px;
  }

  .checkbox-container {
    display: flex;
    align-items: center;
    gap: 8px;
    width: 100%;
  }

  .checkbox-container input[type="checkbox"] {
    width: 16px;
    height: 16px;
    background: #008abf;
    border-radius: 4px;
    margin: 0;
    cursor: pointer;
  }

  .checkbox-container label {
    font-family: "Inter", sans-serif;
    font-size: 14px;
    color: #1e1e1e;
    cursor: pointer;
  }

  .social-login-buttons .btn {
    width: 100%;
    margin-bottom: 10px;
    font-size: 1rem;
  }
  .or-divider {
    display: flex;
    align-items: center;
    text-align: center;
    margin: 20px 0;
  }
  .or-divider::before,
  .or-divider::after {
    content: "";
    flex: 1;
    border-bottom: 1px solid #dcdcdc;
  }
  .or-divider::before {
    margin-right: 0.25em;
  }
  .or-divider::after {
    margin-left: 0.25em;
  }
  .forgot-email {
    display: block;
    margin-top: 5px;
    margin-bottom: 10px;
  }
  .btn-primary-custom {
    background-color: #196aac;
    border-color: #196aac;
    width: 100%;
    font-size: 1rem;
    
    border-bottom: 1vh;
  }
  .msTeams {
    width: 100%;
    border-top: 1vh;
  }
  hr {
    background-color: #6c757d;
    opacity: 40%;
    width: 70%;
  }

  .footer-links {
    text-align: center;
    margin-top: 20px;
  }
  .footer-links a {
    color: #6c757d;
    margin: 0 10px;
    text-decoration: none;
  }