.container-fluid-details {
    padding-top: 20px; /* Space to avoid overlap with fixed header */
    padding-bottom: 20px; /* Space to avoid overlap with fixed footer */
    padding-left: 20px
}
.tab-content {
    background-color: #ffffff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    margin-bottom: 20px;
}
.nav-tabs .nav-link {
    border-radius: 0;
}
.nav-tabs .nav-link.active {
    background-color: #196aac;
    color: white;
}
.btn-primary dropdown-toggle {
    display: flex;
    align-items: center;
    cursor: pointer;
    background-color: #196aac;
    color: white;
    padding: 10px 15px;
    transition: background-color 0.2s ease;
    width: 200px;
    justify-content: space-between;
    border-bottom: 1px solid #ccc; 
}
.dropdown {
    position: relative;
    display: inline-block;
}