/* HEADER */
.header, .footer {
  background-color: #ffffff;
  box-shadow: 10px 4px 8px rgba(0, 0, 0, 0.2);
  padding: 20px;
  z-index: 1000;
}
.header {
  height: 10vh;

  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.header .logo {

  height: 2vw;
  width: 2vw;
}

.caret {
  margin-left: 10px;
  border-top: 5px solid white;
  border-right: 5px solid transparent;
  border-left: 5px solid transparent;
  transition: transform 0.2s ease;
}
.menu {
  display: none;
  position: absolute;
  background-color: #ffffff;
  border: 1px solid #ccc;
  border-radius: 0px;
  width: 200px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  list-style-type: none;
  padding: 0;
  z-index: 1000;
  margin-top: 0;
}

.menu li {
  display: flex;
  align-items: center;
  padding: 10px;
  cursor: pointer;
  transition: background-color 0.2s ease;
  font-size: 16px;
}
.menu li .border-bottom {
  border-bottom: 1px solid #ccc;
}

.menu li:hover {
  background-color: #f1f1f1;
}
.menu li.active {
  background-color: #e9ecef;
}
.menu li i.bi {
font-size: 20px;
  margin-right: 5px; 
  width: 20px;
  height: 20px;
  display: inline-block; 
  line-height: 20px; 
}

.text-link {
  color: inherit;
  text-decoration: inherit;
}

.logout-btn {
  color: red
}

.logout-btn:hover {
  color: darkred;
  text-decoration: underline darkred;
}

.dropdown {
  position: relative;
  display: inline-block;
}
.select {
  display: flex;
  align-items: center;
  cursor: pointer;
  background-color: #196aac;
  color: white;
  padding: 10px 15px;
  transition: background-color 0.2s ease;
  width: 200px;
  justify-content: space-between;
  border-bottom: 1px solid #ccc; 
}
.select:hover {
  background-color: #155a8a;
}

/* FOOTER */

.footer-container {
  min-height: 100%;
  position: relative;
}

.footer {
  height: 15vh;
  bottom: 0;

  background-color: #196aac;
  color: #b1c1cf;
  padding: 15px 0;
  text-align: center;
}
.footer-links a {
  color: #b1c1cf;
  margin: 0 10px;
  text-decoration: none;
}
.footer-links a:hover {
  text-decoration: underline;
}

.school-name {
  margin-left: 10px;
  font-size: 1.5vw;
}

.school-logo {
  width: 100px;
  height: 100px;
  top: 15px;
  left: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}



/* SHARED CLASSES */
.filter-block {
  width: 95vw;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}