body {
    font-family: 'Helvetica', sans-serif;
    background-color: #f8f9fa;
}
.container-fluid-user {
    padding-bottom: 20px;
    text-align: center;
}
.modal {
    text-align: left;
}
.img {
    border-radius: 8px;
    max-height: 100px;
    max-width: 100px;
    margin-right: 10px;
    object-fit: cover;
    float: left; 
}
/* Fix the width of the table */
.table {
    table-layout: fixed;
    width: 100%; /* Adjust as needed, e.g., 80% for smaller tables */
}
.table td{
    vertical-align: middle; 
    padding-left: 10px;
}
.table td:first-child {
    vertical-align: middle;
    display: flex;
    align-items: center
}
.table td:nth-child(2) {
    width: 100%;
    text-align: center; 
}
.table tr {
    height: 100%; 
}
.table img {
    border-radius: 8px;
    max-height: 100px;
    max-width: 100px;
    margin-right: 10px;
    object-fit: cover;
    float: left; 
}

.btn-primary {
    /*align-items: center;*/
    background-color: #196aac;
    border-color: #196aac;
    /*width:100%;*/
}
.btn-secondary{
    /*align-items: center;*/
    background-color: #196aac;
    border-color: #196aac;
    /*margin-right: 10px;*/
}

.popup {
    width: 40%;
    padding: 20px;
    background-color: white;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
}

.popup-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.popup-header h2 {
    margin: 0;
    font-size: 32px;
    letter-spacing: 1px;
}

.pupup-close {
    font-size: 30px;
    cursor: pointer;
}

hr {
    margin: 10px 0;
    border: 0;
    border-top: 1px solid #1E1E1E;
}

.popup {
display: flex;
flex-direction: column;
}

.popup-content {
    display: flex;
    justify-content: space-between;
}

.popup-info {
    width: 60%;
}

.popup-info p strong {
display: inline-block;
margin-bottom: 5px;
font-size: 18px; 
font-weight: bold;
color: #000000; 
}

.popup-info p {
font-size: 15px;
margin: 8px 0px;
color: #757575;
}


.popup-image {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.popup-image img {
    max-width: 100%;
    height: auto;
    border-radius: 5px;

    max-height: 594px;
    width: 420px;
}

.pupup-close-btn {
    height: 100%;
    width: 30%;
    padding: 10px 20px;
    background-color: #196AAC;
    color: white;
    font-size: 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    align-self: flex-end;
}

.pupup-close-btn:hover {
    background-color: #0056b3;
}
