body {
    font-family: 'Helvetica', sans-serif;
    background-color: #f8f9fa;
}
.filter-block {
    padding: 2vh 1vw;
}
.filter-block h3 {
    margin: 0;
}
.card img {
    height: 150px;
    object-fit: cover;
    border-radius: 5px;
}
.card {
    border-radius: 8px;
    box-shadow: 4px 8px 12px rgba(0, 0, 0, 0.1);
}
.card-title {
    font-size: 1.25rem;
    font-weight: bold;
}
.card-info {
    font-size: 1rem;
    margin-top: 10px;
}
.card-info i {
    margin-right: 5px;
    color: #6c757d;
}
.btn-primary {
    background-color: #196aac;
    border-color: #196aac;
}
.btn-secondary {
    background-color: #6c757d;
    border-color: #6c757d;
}

.popup {
    width: 40%;
    padding: 20px;
    background-color: white;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
}

.popup-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.popup-header h2 {
    margin: 0;
    font-size: 32px;
    letter-spacing: 1px;
}

.pupup-close {
    font-size: 30px;
    cursor: pointer;
}

hr {
    margin: 10px 0;
    border: 0;
    border-top: 1px solid #1E1E1E;
}

.popup {
display: flex;
flex-direction: column;
}

.popup-content {
    display: flex;
}

.popup-info {
    width: 60%;
    margin: 1vh 1vw;
}

.popup-info p strong {
display: inline-block;
margin-bottom: 5px;
font-size: 18px; 
font-weight: bold;
color: #000000; 
}

.popup-info p {
font-size: 15px;
margin: 8px 0px;
color: #757575;
}


.popup-image {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    margin: 1vh 1vw;
}

.popup-image img {
    max-width: 100%;
    height: auto;
    border-radius: 5px;

    max-height: 594px;
    width: 420px;
}

.pupup-close-btn {
    height: 100%;
    width: 30%;
    padding: 10px 20px;
    background-color: #196AAC;
    color: white;
    font-size: 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    align-self: flex-end;
}

.pupup-close-btn:hover {
    background-color: #0056b3;
}

/* JESUS CHRIST */

.course-container {
    display: flex;
    justify-content: center;

    margin-right: 5vw;
}

@media only screen and (max-width: 800px) {
    .course-container {   
        flex-direction: column-reverse;
    }

    .course-card-container.signed {
        width: 100vw;
        margin-left: 0;
    }

    .right-panel {
        width: 100vw;
        position: relative;

        margin-bottom: 2vh;
    }

    /* popup */
    .popup-content {
        flex-direction: column-reverse;
        align-items: center;
        justify-content: center;
        text-align: center;
    }

    .popup-info {
        margin-top: 2vh;
    }
  }

  @media only screen and (min-width: 800px) {
    .course-container {
        flex-direction: row;
    }

    .course-card-container.signed {
        width: 75vw;
        margin-left: 0;
    }

    .right-panel {
        position: sticky;
        width: 20vw;

        max-height: 90vh;
        top: 10vh;
        right: 0;
    }

    /* popup */
    .popup-content {
        justify-content: space-between;
    }
  }





.right-panel {
    background-color: #fff;
    border: 1px solid #ddd;
    padding: 20px;
    height: fit-content;
    overflow-y: scroll;
    overflow-x: hidden;
    box-shadow: -2px -2px 5px rgba(0, 0, 0, 0.1);
}
.right-panel h4 {
    font-size: max(1vw,2vh);
    font-weight: bold;
    margin-top: 10px;
    margin-bottom: 10px;
}
.registered-card {
    display: flex;
    flex-direction: column;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 10px;
    margin-bottom: 15px;
    box-shadow: 0 1px 10px rgba(0, 0, 0, 0.1);
    background-color: white;
    width: 100%;
}
.registered-card img {
    height: max(5vw,10vh);
    width: max(5vw,10vh);
    border-radius: 8px;
    margin-bottom: 10px;
}
.registered-card .card-info {
    font-size: 0.9rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-bottom: 10px;
}
.registered-card .btn {
    width: 100%;
    margin-top: 5px;
}


